.main {
    padding: 20px;
    height: 100vh;
}

.sidebar {
    background-color: #005555;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    margin-right: 20px;
    min-height: 100%;
    padding: 10px;
    /* width: 300px; */
}



.content {
    width: 100%;
    height: 100%;
}

.header {
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    margin-bottom: 20px;
    height: 10vh;
    /* width: 100%; */
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.body {
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    height: 82vh;
    width: 100%;
    padding: 15px;
    overflow-x: scroll !important;

}

.menu {
    margin-top: 100px;
    padding: 0 10px;
}

.menu-item {
    margin-top: 30px;

}

.logo {
    color: white;
    font-weight: bold;
}

.role {
    color: white;
    font-size: 14px;
    margin-top: -13px;
}

.menu-item a {
    color: rgba(255, 255, 255, 0.749);
    text-decoration: none;
    font-size: 18px;
    padding: 0 10px;
}

.menu-item i {
    color: rgba(255, 255, 255, 0.776);
    text-decoration: none;
    font-size: 18px;
    margin: 0 15px;
}

.active-menu-item {
    color: white;
    background-color: #013737;
    padding: 5px;
    border-radius: 5px;
}

.header-action-icon {
    font-size: 30px;
    color: black;
    cursor: pointer;
    padding-left: 10px;
}



@media screen and (max-width: 768px) {
    .menu {
        padding: 0 5px;
    }
}